/* Main container */
.container {
  padding: 10px;
  font-family: 'Digital-7'; /* Apply the digital font globally */
}

/* Grid layout */
.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr)); /* Responsive grid */
  gap: 15px;
}

/* Individual tile styling */
.tile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 15px; /* Increased padding for a better layout */
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  background-color: #fff; /* Default background */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.tile:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

/* Text styling for numerical values */
.tile p {
  font-size: 32px; /* Increased font size for better visibility */
  font-weight: bold;
  margin: 5px 0 0;
  color: #000; /* Default text color */
  font-family: 'Digital-7'; /* Ensure numerical values use digital font */
}

/* Label styling */
.tile h4 {
  font-size: 14px; /* Slightly increased font size */
  margin: 0;
  color: #666; /* Subtle gray for labels */
}

/* Image styling for icons */
.tile img {
  width: 50px; /* Increased size for better visibility */
  height: 50px;
  margin-bottom: 5px;
}

/* Icon styling */
.tile .icon {
  font-size: 50px; /* Consistent icon size */
  margin-bottom: 5px;
}

/* Responsive hover effect for tiles */
@media (hover: hover) {
  .tile:hover {
    transform: scale(1.05);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  }
}

/* Adjustments for dark mode */
.container.dark .tile {
  background-color: #333;
  color: #fff;
  border: 1px solid #555;
}

.container.dark .tile h4 {
  color: #bbb;
}

.container.dark .tile p {
  color: #fff;
}
